html {
  font-size: 1.5vw;
}

@media (max-width: 750px) {
  html {
    font-size: 2.2vw;
  }
}

body {
  font: 100%/1.2 sans-serif;
  font-family: Heebo, arial, sans-serif;
  font-size: 18px;
  font-weight: 400;
  background-color: #fff;
  color: #0e283c;
  direction: ltr;
}

@media (max-width: 1700px) {
  body {
    font-size: calc( .8vw + 6px);
  }
}

@media (max-width: 1000px) {
  body {
    font-size: calc( 1vw + 6px);
  }
}

@media (max-width: 750px) {
  body {
    font-size: 4.25vw;
  }
}

#wrapper {
  overflow: hidden;
  direction: rtl;
}

@media (max-width: 1175px) {
  img {
    max-width: 100%;
  }
}

.loading-ring {
  display: inline-block;
  width: 44px;
  height: 44px;
}

.loading-ring:after {
  content: " ";
  display: block;
  width: 26px;
  height: 26px;
  margin: 1px;
  border-radius: 50%;
  border: 4px solid #8dba06;
  border-color: #8dba06 transparent #8dba06 transparent;
  animation: loading-ring 1.2s linear infinite;
}

@keyframes loading-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

body.blur .od-wrapper {
  filter: blur(5px);
}

.overlay {
  background: rgba(7, 30, 51, 0.6);
  position: fixed;
  overflow: hidden;
  z-index: 999999999999;
  padding: 10px 60px;
  direction: rtl;
}

.overlay .pad {
  max-width: 985px;
  width: 100%;
  max-height: 740px;
  height: 100%;
  position: relative;
  z-index: 4;
}

.lb_cover {
  width: 100%;
}

.lb_slide-inner {
  padding: 77% 0 0 0;
  overflow: hidden;
  position: relative;
}

.lb_slide-inner img {
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}

.lb_iframe_hold {
  position: absolute;
  max-width: 1300px;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.big_close {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.lb_btn_hold, .lb_display {
  vertical-align: middle;
}

.lb_btn_hold {
  width: 30px;
}

.lb_display {
  text-align: center;
  max-width: 100%;
  border: 5px solid #fff;
  background: #000;
}

.lb_btn_hold {
  position: absolute;
  top: 0;
  width: 50%;
  height: 100%;
  z-index: 77;
  cursor: pointer;
  -webkit-user-select: none;
}

.lb_btn_hold.lb_prev {
  left: 0;
}

.lb_btn_hold.lb_next {
  right: 0;
}

.lb_btn {
  -webkit-user-select: none;
  cursor: pointer;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 0;
  text-align: center;
  z-index: 99;
  width: 30px;
  height: 43px;
}

.lb_btn:after {
  content: '';
  width: 22px;
  height: 22px;
  border-left: 2px solid #fff;
  border-bottom: 2px solid #fff;
  position: relative;
  display: inline-block;
  vertical-align: top;
  top: 10px;
}

.lb_btn.lb_btn_next {
  left: -43px;
}

.lb_btn.lb_btn_next:after {
  right: -2px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}

.lb_btn.lb_btn_prev {
  right: -43px;
}

.lb_btn.lb_btn_prev:after {
  right: 2px;
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  -ms-transform: rotate(-135deg);
  -moz-transform: rotate(-135deg);
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}

.lb_display .lb_slide-inner.media_type_iframe {
  height: 100%;
  position: relative;
  height: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 100%;
}

.lb_display .lb_slide-inner.media_type_iframe iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.lb_close-hold {
  margin: 0 auto;
}

.lb_close {
  z-index: 78;
  cursor: pointer;
  width: 20px;
  height: 30px;
  position: relative;
  float: left;
}

.lb_close:after, .lb_close:before {
  content: "";
  width: 2px;
  height: 20px;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  background: #fff;
}

.lb_close:after {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}

.lb_close:before {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}

.lb_btn_hold .btn {
  position: absolute;
  top: 0;
  width: 30px;
  height: 30px;
}

.lb_display {
  -webkit-transition: opacity 150ms ease-out;
  -o-transition: opacity 150ms ease-out;
  -moz-transition: opacity 150ms ease-out;
  transition: opacity 150ms ease-out;
}

.lb_display.fadeOut {
  opacity: 0;
}

.lb_display.fadeIn {
  opacity: 1;
}

.lb_figcaption {
  position: absolute;
  bottom: 10px;
  text-align: center;
  width: 100%;
  font-size: 12px;
  font-family: arial;
  color: #A8A8A8;
}

.slick-slide {
  overflow: hidden;
  position: relative;
}

.lb_content img {
  max-height: 100%;
  max-width: 100%;
  position: absolute;
  margin: 0 .7rem;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  border: 3px solid #fff;
}

.media_type-iframe {
  height: 0 !important;
  padding: 60% 0 0 0;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  margin: auto 0;
}

.lb_content iframe {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
}

.slick-slide {
  outline: none;
}

@media (max-width: 750px) {
  .overlay {
    padding: 10px 25px;
  }
  .lb_btn:after {
    width: 14px;
    height: 14px;
  }
  .lb_btn.lb_btn_prev {
    right: -30px;
  }
  .lb_btn.lb_btn_next {
    left: -30px;
  }
}

.js-popup {
  position: fixed;
  z-index: 9999999;
  background: rgba(26, 38, 50, 0.75);
  direction: rtl;
  padding: 3.33em 1rem;
  max-height: 100%;
}

.popup-body {
  background: #fff;
  padding: 1.11em;
  border-radius: 0.27em;
  text-align: right;
  max-height: 100%;
  max-height: 100%;
  overflow: hidden;
}

.popup-scroller {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

.js-popup-btn-close {
  position: absolute;
  bottom: 100%;
  left: 0;
  margin-bottom: 0.66em;
  width: 27px;
  height: 27px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}

.js-popup-btn-close:before, .js-popup-btn-close:after {
  content: "";
  background: #fff;
}

.js-popup-btn-close:before {
  width: 100%;
  height: 2px;
}

.js-popup-btn-close:after {
  width: 2px;
  height: 100%;
}

.is-popup-active #wrapper {
  filter: blur(6px);
}

